<template>
  <div class="pageContent"> 
    <el-form :model="ruleForm" ref="ruleForm" label-width="140px" class="demo-ruleForm">
      <!-- 基本信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">基本信息</div>
        <div class="flex-row-start1">
          <div style="width:387px">
            <div style="border:1px solid #DDD">
              <video
                v-if="ruleForm.videoForm && ruleForm.videoForm.length == 1"
                slot="reference"
                :src="ruleForm.videoForm[0].imageUrl"
                style="width:380px;height:380px;"
                class="avatar video-avatar"
                controls="controls">
                您的浏览器不支持视频播放
              </video>
              <el-image
                v-else 
                style="width: 380px; height: 380px"
                :src="ruleForm.goodsImg[0].imageUrl">
              </el-image>
            </div>
            <div style="display:flex;justify-content:space-around;margin:15px 0">
              <div v-for="(item,i) in ruleForm.goodsImg" :key="i">
                <div class="demo-image__preview">
                  <el-image style="width: 60px; height: 60px" :src="item.imageUrl" :preview-src-list="[item.imageUrl]"></el-image>
                </div>  
              </div>
            </div>
          </div>
          <div style="flex:1">
            <el-form-item label="【商品名称】">
              <div>{{ruleForm.name}}</div>
            </el-form-item>
            <el-form-item label="【商品描述】">
              <div>{{ruleForm.abstracts}}</div>
            </el-form-item>
            <el-form-item label="【分享描述】">
              <div>{{ruleForm.shareDescription}}</div>
            </el-form-item>
            <el-form-item label="【商品编码】">
              <div>{{ruleForm.code}}</div>
            </el-form-item>
            <el-form-item label="【商品分类】">
              <div>{{ruleForm.productCategoryName}}</div>
            </el-form-item>
            <el-form-item label="【物流运费】">
              <div>{{ruleForm.companyNane}}</div>
            </el-form-item>
          </div>
        </div>
      </el-card>
      <!-- 规格 & 价格 & 库存 -->
      <el-card class="box-card gg" style="margin-bottom:20px;">
        <div class="pageHead">规格 & 价格 & 库存</div>
        <!-- 单规格 -->
        <div v-if="ruleForm.skuType == 0">
          <el-form-item label="成本价：">
            <div>{{skuType0.originPrice ? skuType0.originPrice : '-'}}</div>
          </el-form-item>
          <el-form-item label="划线价：">
            <div>{{skuType0.linePrice ? skuType0.linePrice : '-'}}</div>
          </el-form-item>
          <el-form-item label="销售价：">
            <div>{{skuType0.salePrice}}</div>
          </el-form-item>
          <el-form-item label="库存：">
            <div>{{skuType0.inventory}}</div>
          </el-form-item>
          <el-form-item label="单位：">
            <div>{{skuType0.unit}}</div>
          </el-form-item>
          <el-form-item label="规格编码：">
            <div>{{skuType0.skuCode ? skuType0.skuCode : '-'}}</div>
          </el-form-item>
          <el-form-item label="销量：">
            <div>{{skuType0.saleNum ? skuType0.saleNum : '0'}}</div>
          </el-form-item>
        </div>
        <!-- 多规格 -->
        <div class="info_box" v-if="ruleForm.skuType == 1">
          <!-- 明细表 -->
          <div class="spec_con">
            <span class="name">规格明细：</span>
            <!-- 一、二级规格 -->
            <div class="spec_box" style="padding:0;border-bottom:0 none;">
              <div class="title_box">
                <span class="one_spec_name">{{specArr[0] && specArr[0].specName}}</span>
                <span class="two_spec_name"  v-if="specArr[1] && specArr[1].specName">{{specArr[1] && specArr[1].specName}}</span>
                <span class="origin_price_name">成本价</span>
                <span class="line_price_name">划线价</span>
                <span class="sale_price_name"><i style="color:red;">* </i> 销售价</span>
                <span class="inventory_name"><i style="color:red;">* </i> 库存</span>
                <span class="unit_name"><i style="color:red;">* </i> 单位</span>
                <span class="specNo_name">规格编码</span>
                <span class="specNo_name">销量</span>
              </div>
              <div class="rows" v-for="(item,index) in specData" :key="index">
                <p class="td">{{item.specValue}}</p>
                <p class="td border" v-if="specArr[1] && specArr[1].specName">
                  <span class="color" v-for="(nameChild,nameIdx) in item.children" :key="nameIdx" v-show="nameChild.specValue">{{nameChild.specValue}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(priceChild,priceIdx) in item.children" :key="priceIdx">{{priceChild.originPrice ? priceChild.originPrice : '-'}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(lineChild,lineIdx) in item.children" :key="lineIdx">{{lineChild.linePrice ? lineChild.linePrice : '-'}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(saleChild,saleIdx) in item.children" :key="saleIdx">{{saleChild.salePrice ? saleChild.salePrice : '-'}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(invenChild,invenIdx) in item.children" :key="invenIdx">{{invenChild.inventory}}</span>
                </p>
                <p class="td flex2">
                  <span class="color" v-for="(unitChild,unitIdx) in item.children" :key="unitIdx">{{unitChild.unit}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(noChild,noIdx) in item.children" :key="noIdx">{{noChild.skuCode ? noChild.skuCode : '-'}}</span>
                </p>
                <p class="td">
                  <span class="color" v-for="(numberChild,numIdx) in item.children" :key="numIdx">{{numberChild.saleNum ? numberChild.saleNum : 0}}</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </el-card>
      <!-- 商品参数 -->
      <el-card class="box-card" style="margin-bottom:20px;" v-if="ruleForm.parameter && ruleForm.parameter.length > 0">
        <div class="pageHead">商品参数</div>
        <el-form-item label="">
          <el-table :data="ruleForm.parameter" style="width: 800px">
            <el-table-column label="参数名" width="350" prop="key"></el-table-column>
            <el-table-column label="参数值" width="350" prop="value"></el-table-column>
          </el-table>
        </el-form-item>
      </el-card>
      <!-- 商品详情 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">商品详情</div>
        <div v-html="ruleForm.content"></div>
      </el-card>
      <!-- 其他信息 -->
      <el-card class="box-card">
        <div class="pageHead">其他信息</div>
        <el-form-item label="销量显示：">
          <div>{{ruleForm.showSales == 1 ? '开' : '关'}}</div>
        </el-form-item>
        <el-form-item label="评价显示：">
          <div>{{ruleForm.evaluate == 1 ? '开' : '关'}}</div>
        </el-form-item>
      </el-card>
    </el-form>
    <div style="text-align:center;">
      <el-button type="primary" @click="back">返 回</el-button>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getProductInfoById } from '@/api/commodity/commodityList'
export default {
  data(){
    return{
      ruleForm:{
        parameter:[],
        goodsImg:[{imageUrl:''}]
      },
      specData:{}
    }
  },
  created(){
    this.getData();
  },
  methods:{
    getData(){
      getProductInfoById(this.$route.query.productId).then(res=>{
        console.log('商品详情',res);
        if(res.code == 200){
          this.ruleForm = res.data
          // 规格
          if(res.data.skuType == 0) this.skuType0 = res.data.productSkuList[0]
          // 多规格
          if(res.data.skuType == 1){
            this.specArr = res.data.specArr
            this.specData = res.data.productSkuList
          }
        }
      })
    },
    back(){
      window.history.back()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop',
    ])
  },
}
</script>
<style lang="less" scoped>
.li{
  width: 100%;
  margin-bottom: 15px;
}
.info_box {
  width: 100%;
  background: rgba(255, 255, 255, 1);
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 16px;
  .spec_con{
    width: 100%;
    display: flex;
    margin-top: 20px;
    .name {
      box-sizing: border-box;
      padding-right: 12px;
      width: 140px;
      text-align: right;
      font-weight: bold;
      font-size: 14px;
      color: #606266;
      &::before {
        content: '*';
        display: inline-block;
        color: #f56c6c;
      }
    }
    .spec_box {
      width: 60%;
      margin-left: 15px;
      border: 1px solid #dddddd;
      padding: 10px;
      box-sizing: border-box;
      .spec_row_box {
        width: 100%;
        display: flex;
        flex-flow: column;
        .spec_name_box {
          width: 100%;
          margin-bottom: 20px;
          background-color: #f8f8f8;
          &::after {
            content: "";
            display: block;
            clear: both;
          }
          .spec_name {
            float: left;
            width: 80px;
            padding: 7px 10px;
            margin: 0;
            font-size: 12px;
            line-height: 16px;
            font-weight: 400;
            height: 100%;
          }
          input {
            font-size: 12px;
            float: left;
            width: 94px;
            background-color: #fff;
            border: 0 none;
            outline: none;
            border: 1px solid #bbb;
            border-radius: 2px;
            cursor: pointer;
            padding: 5px 10px;
            box-sizing: border-box;
            &::placeholder {
              font-size: 12px;
              line-height: 100%;
            }
          }
          .close_btn {
            float: right;
            width: 18px;
            height: 18px;
            line-height: 18px;
            text-align: center;
            color: #fff;
            background-color: rgba(0, 0, 0, 0.5);
            border-radius: 50%;
            margin-top: 6px;
            cursor: pointer;
          }
        }
        .spec_value_box {
          width: 100%;
          margin-bottom: 20px;
          &::after {
            content: "";
            display: block;
            clear: both;
          }
          .spec_value_name {
            width: 80px;
            padding: 7px 10px;
            margin: 0;
            font-size: 12px;
            line-height: 16px;
            font-weight: 400;
            height: 100%;
            float: left;
          }
          .right_box {
            float: left;
            width: 85%;
            .input_box {
              float: left;
              display: flex;
              position: relative;
              margin: 0 10px 10px 0;
              &:hover {
                .close_spec_value {
                  display: block;
                }
              }
              input {
                font-size: 12px;
                box-sizing: border-box;
                border: 1px solid #bbb;
                border-radius: 2px;
                min-height: 30px;
                max-height: 76px;
                line-height: 100%;
                outline: none;
                padding: 5px 10px;
                width: 152px;
                background-color: #fff;
                &::placeholder {
                  font-size: 12px;
                  line-height: 100%;
                }
              }
              .close_spec_value {
                position: absolute;
                right: -9px;
                top: -9px;
                width: 18px;
                height: 18px;
                line-height: 18px;
                text-align: center;
                color: #fff;
                background-color: rgba(0, 0, 0, 0.2);
                border-radius: 50%;
                cursor: pointer;
                display: none;
                &:hover {
                  background-color: rgba(0, 0, 0, 0.5);
                }
              }
            }
            .add_spec_value {
              float: left;
              height: 30px;
              line-height: 30px;
              text-align: center;
              font-size: 12px;
              color: #3388ff;
              cursor: pointer;
              font-style: normal;
              margin-left: 10px;
            }
          }
        }
      }
      .add_spec_btn {
        max-width: 80px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        cursor: pointer;
        margin-left: 12px;
        padding: 0px 5px;
        box-sizing: border-box;
        background-color: rgb(247, 247, 247);
        color: rgb(101, 113, 128);
        border-radius: 3px;
        border: 1px solid rgb(215, 221, 228);
        font-size: 12px;
        &.default {
          cursor: default;
          background-color: #f7f7f7;
          color: #ccc;
          pointer-events: none;
          &:hover {
            background-color: #f7f7f7;
            color: #ccc;
            border: 1px solid rgb(215, 221, 228);
          }
        }
        &:hover {
          color: #09f;
          border: 1px solid #09f;
        }
      }
      .title_box,
      .rows {
        width: 100%;
        min-height: 49px;
        line-height: 49px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ccc;
        span,
        .td {
          margin: 0;
          display: block;
          height: 100%;
          font-size: 12px;
          color: #657180;
          flex: 1;
          text-align: center;
          &.border {
            border-left: 1px solid #ccc;
          }
          .specNo_name,
          .flex2 {
            flex: 2;
          }
        }
        input {
          width: 80px;
          height: 30px;
          border-radius: 2px;
          border: 1px solid #ccc;
          outline: none;
          cursor: pointer;
          text-align: center;
          line-height: 30px;
        }
      }
    }
  }
  .set_box{
    width: 100%;
    display: flex;
    margin-top: 20px;
    .set_title{
      box-sizing: border-box;
      padding-right: 12px;
      width: 140px;
      text-align: right;
      font-weight: bold;
      font-size: 14px;
      color: #606266;
    }
    .spec_box{
      width: 60%;
      margin-left: 15px;
      border: 1px solid #dddddd;
      padding: 20px;
      box-sizing: border-box;
      .line{
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        &:nth-of-type(2){
          margin: 0;
        }
        .value_box{
          margin-right: 20px;
          .key{
            font-size: 14px;
            color: #657180;
          }
          input{
            margin-left: 15px;
            width: 200px;
            height: 30px;
            border-radius: 2px;
            border: 1px solid #ccc;
            outline: none;
            cursor: pointer;
            text-align: center;
            line-height: 30px;
          }
        }
      }
      .btn_box{
        width: 100%;
        display: flex;
        justify-content: flex-end;
        .btn{
          width: 80px;
          text-align: center;
          height: 30px;
          line-height: 30px;
          cursor: pointer;
          margin-left: 12px;
          padding: 0px 5px;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          background-color: #09f;
          color: #fff;
          border-radius: 3px;
          border: 1px solid #d7dde4;
          font-size: 12px;
          &.cancel_btn{
            margin-right: 10px;
            background-color: #FF4444;
          }
        }
      }
    }
  }
}
</style>